:root {
 --theme-100: #536dfe;
 --theme-200: #536dfe22;
 --text-100: #000000;
 --text-200: #3c4043;
 --text-300: #585b63;
 --text-400: #fcfbfb;
 --text-500: #ffffff;
 --surface-100: #deeafdb4;
 --surface-200: #f4f4f4;
 --surface-300: #f6f6f6;
 --surface-400: #fbfafb;
 --surface-500: #fcfcff;
 --surface-600: #f8f9fa;
 --surface-700: #f7f9fcf9;
 --surface-800: #ffffff;
 --surface-900: #f7f9fc;
 --border-100: #d2d3d7;
 --border-200: #f0f1f3;
 --scrim-100: #00000098;
 --scrim-200: #00000080;
 --shadow-100: 0 0 #0000, 0 0 #0000, 0 1px 3px 0 rgba(0,0,0,.1), 0 1px 2px -1px rgba(0,0,0,.1);
 --shadow-200: rgba(0, 0, 0, 0.1) 0px 1px 3px 0px, rgba(0, 0, 0, 0.06) 0px 1px 2px 0px;
}

* {
 box-sizing: border-box;
 margin: 0;
 padding: 0;
}

*::before,
*::after {
 box-sizing: inherit;
}

html,
body {
 font-size: clamp(.925em, .975em + .12vw, 1.325em);
 scroll-behavior: smooth;
}

body {
 font-family: "Plus Jakarta Sans", system-ui, sans-serif;
 color: var(--text-200);
 user-select: none;
 text-rendering: optimizeLegibility;
 text-size-adjust: 100%;
 touch-action: manipulation;
 -ms-touch-action: manipulation;
 -moz-osx-font-smoothing: grayscale;
 -webkit-text-size-adjust: 100%;
 -webkit-user-select: none;
 -webkit-font-smoothing: antialiased;
 -webkit-tap-highlight-color: transparent;
}

::placeholder {
 color: transparent;
}

img {
 max-width: 100%;
}

ul {
 list-style: none;
}

a {
 text-decoration: none;
 cursor: pointer;
}

button {
 border: none;
 outline: none;
 cursor: pointer;
 background: transparent;
}

article:nth-of-type(1) .container {
 background: linear-gradient(rgba(4, 9, 30, 0.6), rgba(4, 9, 30, 0.6)), url("./assets/conference-room-lg.webp") no-repeat center center / cover;
}

article:nth-of-type(2) .container {
 background: linear-gradient(rgba(4, 9, 30, 0.6), rgba(4, 9, 30, 0.6)), url("./assets/posed-lg.webp") no-repeat center center / cover;
}

article:nth-of-type(3) .container {
 background: linear-gradient(rgba(4, 9, 30, 0.6), rgba(4, 9, 30, 0.6)), url("./assets/generic-1-lg.webp") no-repeat center center / cover;
}

article:nth-of-type(4) .container {
 background: linear-gradient(rgba(4, 9, 30, 0.6), rgba(4, 9, 30, 0.6)), url("./assets/generic-2-lg.webp") no-repeat center center / cover;
}

article:nth-of-type(5) .container {
 background: linear-gradient(rgba(4, 9, 30, 0.6), rgba(4, 9, 30, 0.6)), url("./assets/generic-3-lg.webp") no-repeat center center / cover;
}

@media screen and (min-width: 768px) {
 body {
  overflow: auto !important;
 }
}